































import { Component, Vue } from 'vue-property-decorator';
import { Header, Loading } from '@/components';
import { getMusicArticleDetail, getTrackInfo } from '../api';

@Component({
  components: {
    YHeader: Header,
    Loading,
  },
  name: 'detail',
})
export default class MusicArticleDetail extends Vue {
  public $refs !: {
    content: Element,
    audio: HTMLAudioElement,
  };

  private created() {
    this.loadData(+this.$route.params.id);
  }

  private data() {
    return {
      data: null,
    };
  }

  private async loadData(id: number) {
    const res = await getMusicArticleDetail(id);
    this.data = res;
  }

  private async play(e: any) {
    if (e.target.className === 'playtrack') {
      this.$store.commit('player/play');
      const res = await getTrackInfo(+e.target.id);
      this.$store.commit('player/add', res);
    }
    // this.$router.push(`/player/${e.target.id}`);
  }

  private goBack() {
    this.$router.back();
  }
}
